___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(0, 0, 0);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  font-family: 'Open sans', sans-serif;
  font-weight: 500;
  font-size: 1em;
  max-width: 640px;
  margin: 50px auto;
  background: #e4dede;
  padding: 20px;
  border-radius: 10px;
}

form input {
  padding: 0 10px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form p {
  font-size: 1.1em;
  font-weight: bold;
  font-style: italic;
}

form button {
  border: none;
  background: var(--primary-color);
  color: #eee7e7;
  font-size: 18px;
  font-weight: 600;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
}

input[type="checkbox"]{
  /* desativa a checkbox padrão que não possibilita alteraçao */
  -webkit-appearance: none; 
  background-color: rgb(188, 188, 197);
  width: 20px;
  height: 20px;
  border-radius: 10px;
  margin-top: 1%;
  cursor: pointer;
}

input[type="checkbox"]:checked{
  outline: 1px solid black;
  background-color: rgb(0, 0, 0);
}

form button {
  display: block;
  margin: 40px 0;
  font-size: 1em;
  border-radius: 6px;
}

form button:hover {
  background: rgb(37, 36, 36);
  cursor: pointer;
}

form button:active {
  background: #7d7d81;
  cursor: pointer;
}


.length{
  font-family: 'Open sans', sans-serif;
  font-size: 1em;
  margin: 10px;
  border-radius: 4px;
}

.pwdGenerated {
  font-family: 'Open sans', sans-serif;
  font-size: 2em;
  font-weight: 700;
  margin-bottom: 3%;
  margin-top: 3%;
  word-break: break-all;
}
